.rest{
  padding: 250px 0 60px;
  @include media($def, max){
    padding-bottom: 80px;
  }
  @include media($md, max){
    padding-top: 110px;
  }
  &__head{
    margin-bottom: 28px;
    @include media($md, max){
      margin-bottom: 100px;
      text-align: center;
    }
  }
  &__img{
    max-width: 315px;
    width: 100%;
    position: relative;
    top: -42px;
    margin: 0 auto;
    left: -21px;
    z-index: -1;
    &:before{
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      width: 747px;
      height: 747px;
      background: url('pic/rest_circle.png') center center no-repeat;
      right: 60%;
      top: 50%;
      transform:translateY(-50%);
    }
    &-wrap{
      padding-top: 100%;
      position: relative;

    }
    &-item{
      width: 100%;
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 15px;
      background: #fff;
      padding: 10%;
      z-index: 1;
      box-shadow:  0 0 10px rgba(0,0,0,0.2);
      &--main{
        z-index: 2;
      }
      &--type1{
        width: 52%;
        height: 52%;
        //top: -21%;
        top: 0;
        left: auto;
        //right: -35%;
        right: 0;
        padding: 0;
      }
      &--type2{
        width: 52%;
        height: 52%;
        //bottom: 5%;
        bottom: 0;
        top: auto;
        //left: -40%;
        left: 0;
        padding: 0;
      }
      img{
        height: inherit;
      }
    }
  }

  &__wrap{
    margin-bottom: 161px;
    @include media($def, max){
      margin-bottom: 22px;
    }
    .partners__wrap{
      @include media($def, max){
        padding: 20px 5%;
      }
      @include media($md, max){
        padding: 20px 0;
        text-align: center;
      }
    }
  }
  &__content{
    margin-bottom: 77px;
    @include media($md, max){
      text-align: center;
    }
    &-text{
      max-width: 476px;
      width: 100%;
      margin: 0 auto;
      @include media($md, max){
        margin-bottom: 25px;
      }
    }
  }
  &__footer{
    position: relative;
    @include media($md, max){
      text-align: center;
    }
    &:before{
      content: '';
      position: absolute;
      top: -63px;
      left: -152px;
      width: 337px;
      height: 337px;
      background: url('pic/rest_bg2.png') center center no-repeat;
      background-size: 337px 337px;
    }
  }
}