.spon{
  padding-top: 80px;
  padding-bottom: 200px;
  min-height:799px;
  background: url('pic/sponsy_bg.png') center top no-repeat;
  //background-size: 1792px 799px;
  @media (max-width: 1450px){
    background-position: -500px top;
  }
  @include media($lg, max){
    background-position: -610px top !important;
  }
  @include media($def, max){
    padding-bottom: 130px;
  }
  @include media($md, max){
    background-position: -520px top !important;
  }
  @include media($sm, max){
    padding-bottom: 60px;
  }
  &__main{
    padding-top: 178px;
    @include media($md, max){
      padding-top: 50px;
    }
  }
  &__cnt{
    max-width: 567px;
    min-height: 252px;
    margin-bottom: 84px;
    @include media($def, max){
      margin-bottom: 15px;
      min-height: initial;
      max-width: 100%;
    }

  }
  &__form{
    margin-top: 25px;
    padding: 10px;
    border-radius: 50px;
    background: #fff;
    padding-left: 18px;
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
    .button{
      height: 40px;
      box-shadow: none;
      max-width: 176px;
    }
    input{
      font-weight: normal;
    }
  }

  &__video{
    position: relative;
  }
  &__desktop{
    position: relative;
    top: 42px;
    left: -9%;
    max-width: 1115px;
    width: 153%;
    @media (max-width: 1730px){
      width: 133%;
    }
    @media (max-width: 1510px){
      width: 117%;
    }
    @include media($lg, max){
      width: 100% !important;
      left: 6%;
    }
    &-bg{
      position: relative;
      padding-top: 86.62%;
      background: url('pic/iMac.png') center center no-repeat;
      background-size: contain;
    }
    &-cnt{
      position: absolute;
      top: 3.7%;
      left: 2.8%;
      width: 87.9%;
    }
  }

  &__mobile{
    position: absolute;
    left: -24%;
    bottom: 5%;
    max-width: 119px;
    width: 22%;
    @include media($lg, max){
      left: 0%;
      bottom: -1%;
      width: 17%;
    }
    &-bg{
      position: relative;
      padding-top: 197.66%;
      background: url('pic/sposy__phone.png') center center no-repeat;
      background-size: contain;
    }
    &-cnt{
      position: absolute;
      top: 12.3%;
      left: 9%;
      width: 82.4%;
    }
  }
}