.libra{
  position: relative;
  top: 56px;
  @include media($sm, max){
    top: 0;
    padding-top: 50px;
  }
  &__wrap{
    max-width: 241px;
    width: 100%;
    position: relative;
    @include media($md, max){
      margin: 0 auto 30px;
    }
  }
  &__head{
    position: absolute;
    width: 100%;
    top: 15px;
    left: 0;
  }
  &__line{
    transform: rotate(-15deg);
  }
  &__item{
    position: absolute;
    width: 47%;
    z-index: 1;
    &--1{
      left: -18%;
      top: 13px;
    }
    &--2{
      top: -45px;
      right: -18%;
    }
  }
}