////
/// @group _partials/button.scss
////

// scss
// ================================

.button {
	max-width: 210px;
	width: 100%;
	height: 57px;
	line-height: 55px;
	text-align: center;
	background: #769af9;
	border: none;
	border-radius: 50px;
	background: -moz-linear-gradient(top, #769af9 1%, #4273f4 100%);
	background: -webkit-linear-gradient(top, #769af9 1%,#4273f4 100%);
	background: linear-gradient(to bottom, #769af9 1%,#4273f4 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#769af9', endColorstr='#4273f4',GradientType=0 );
	box-shadow: 7px 9px 1px rgba(0,0,0,0.1);
	span{
		line-height: normal;
		align-items: center;
		font-size: 16px;
		color: #fff;
	}
	&:hover{
		box-shadow: none;
		text-decoration: none;
	}
	&--cType2{
		background: rgb(92,239,159);
		background: -moz-linear-gradient(top, rgba(92,239,159,1) 0%, rgba(26,201,106,1) 100%);
		background: -webkit-linear-gradient(top, rgba(92,239,159,1) 0%,rgba(26,201,106,1) 100%);
		background: linear-gradient(to bottom, rgba(92,239,159,1) 0%,rgba(26,201,106,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5cef9f', endColorstr='#1ac96a',GradientType=0 );
	}
}
