.contact {
  background: url('pic/form_bg.png') left center no-repeat;
  background-position: -240px center;
  padding: 59px 52px 35px;
  border-radius: 15px;
  box-shadow: 0 0 25px rgba(0,0,0,0.1);
  @include media($lg, max){
    padding: 30px 20px;
  }
  @include media($def, max){
    background-position: -290px center;
  }
  @include media($md, max){
    background-position: 0px -100px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 28px;
    align-items: flex-end;
    @include media($md, max){
      align-items: flex-start;
      padding-top: 30px;
    }
  }
  &__item {
    max-width: 255px;
    width: 100%;
    margin-bottom: 28px;
  }
  &__link {
    font-size: 16px;
    color: $c_txt1;

  }
  &__social {
    margin-bottom: 35px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    @include media($md, max){
      justify-content: flex-start;
      margin-left: -15px;
      margin-right: -15px;

    }
    &-item {
      padding: 0 10px;
      @include media($lg, max){
        padding: 0 5px;
      }
    }
    &-link{
      width: 49px;
      height: 49px;
      background: $c_alt2;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      @include media($lg, max){
        width: 45px;
        height: 45px;
      }
      &:hover{
        opacity: 0.8;
      }
      svg{
        fill: #fff;
      }
    }
  }

}