.bubbles{
  width: 210px;
  height: 210px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  @include media($def, max){
    transform: scale(0.7);
  }
  &--sm{
    width: 164px;
    height: 171px;
    .bubbles__title{
      font-size: rem(33);
    }
    .bubbles__text{
      font-size: rem(13);
    }
    .bubbles__icon{
      margin-bottom: 5px;
    }
  }

  &__cont{
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);
  }
  &__icon{
    display: flex;
    justify-content: center;
    svg{
      fill: #fff
    }
    margin-bottom: 15px;
  }
  &__text{
    font-size: 16px;
    color: #fff;
    text-align: center;
    font-weight: bold;
  }
  &__title{
    font-size: rem(57);
    margin-bottom: 10px;
    text-align: center;
    font-family: $fnt_title;
    color: #fff;
  }


}