////
/// @group _mixin/util-colors.scss
////

///  
@mixin util-colors( $map: $palette ) {
	@each $color-name, $color in $map {
		._color-#{$color-name} {
			color: $color
		}
	}
}

///   
@mixin util-bgcolors( $map: $palette ) {
	@each $color-name, $color in $map {
		._bgcolor-#{$color-name} {
			background-color: $color
		}
	}
}

///  
@mixin util-fills( $map: $palette ) {
	@each $color-name, $color in $map {
		._fill-#{$color-name} {
			fill: $color
		}
	}
}
