.wrong{
  display: flex;
  flex-direction: column;
  padding-top: 170px;
  background: url('pic/wrong_bg.jpg') center top no-repeat;
  @include media($md, max){
    padding-top: 80px;
  }
  @include media($sm, max){
    padding-top: 30px;
  }
  &__main{
    min-height: 695px;
    padding-bottom: 65px;
    margin-bottom: -1px;
    background: url('pic/wrong_main.png') center bottom no-repeat;
  }
  &__footer{
    padding: 100px 0 35px;
    background: url('pic/wrong_footer.png') center top no-repeat;
    min-height: 1045px;
    @include media($md, max){
      padding-top: 30px;
      text-align: center;
    }
    &-head{
      max-width: 1113px;
      margin-bottom: 35px;
    }
    .view-text{
      & *{
        color: #fff;
      }
    }
  }

  &__head{
    margin-bottom: 42px;
    min-height: 350px;
    @include media($md, max){
      padding-left: 19%;
      text-align: center;
      min-height: initial;
    }
    @include media($sm, max){
      padding-left: 0;
    }
  }
  &__list {
    @include media($md, max){
      text-align: center;
    }
  }
  &__item{
    margin-bottom: 49px;
    @include media($md, max){
      margin-bottom: 20px;
    }
    &--start{
      .wrong__cnt{
        margin-left: auto;
      }
    }
    &--end{
      .wrong__cnt{
        padding-left: 0;
      }
    }
  }
  &__cnt{
    max-width: 547px;
    padding-left: 7%;
    padding-top: 108px;

    &--type1{
      padding-top: 35px;
    }
    @include media($md, max){
      padding-top: 10px;
      padding-left: 0;
      max-width: 100%;
      width: 100%;
    }
  }

  &__img{
    max-width: 549px;
    position: relative;
    @include media($md, max){
     max-width: 250px;
      margin: 0 auto 20px;
    }
    &-cnt{
      position: relative;
      z-index: 1;
    }
  }
  &__bg{
    padding-top: 78.58%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    &--type1{
      background-image: url('pic/work_bg1.png');
    }
    &--type2{
      background-image: url('pic/work_bg3.png');
    }
    &--type3{
      background-image: url('pic/work_bg2.png');
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 60%;
    width: 100%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%)
  }

  &__line{
    position: absolute;
    width: 100%;
    @include media($md, max){
      display: none;
    }
    svg{
      width: 100%;
    }
    &--type1{
      top: 80%;
      right: 90%;
      @include media($def, max){
        top: 67%;
      }
    }
    &--type2{
      top: 80%;
      left: 90%;
      @include media($def, max){
        top: 65%;
      }
    }
    &--type3{
      top: 60%;
      right: 90%;
      @include media($def, max){
        top: 49%;
      }
    }
    &--type4{
      top: 90%;
      left: 83%;
      @include media($def, max){
        top: 74%;
      }
    }
  }
}
