.token{
  //background: url('pic/token_bg.png') left top no-repeat;
  //min-height: 987px;
  padding-top: 60px;
  &__cnt{
    min-height: 582px;
    padding-bottom: 21px;
    position: relative;
    @include media($md, max){
      min-height: initial;
    }
  }
  &__footer{
    padding: 10px 0;
    @include media($md, max){
      text-align: center;
    }
  }

  &__icon{
    padding-top: 122px;
    position: relative;
    @include media($md, max){
      position: absolute;
      max-width: 300px;
      width: 100%;
      opacity: 0.2;
      top: 0;
      left: 0;
      padding-top: 0;
    }
    &:before{
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform:translate(-50%,-50%);
      width: 588px;
      height: 592px;
      z-index: -1;
      background: url('pic/token_bg.png') 0 0 no-repeat;
      @include media($lg, max){
        left: 40%;
      }

    }
  }
  &__iconMain{
    @include media($md, max){
      max-width: 160px;
      margin: 0 auto;
    }
  }
  &__main{
    position: relative;
    max-width: 322px;
    width: 100%;
    margin: 0 auto;
  }
  &__bg{
    padding-top: 101.52%;
    background: url('pic/sps_main.svg') center center no-repeat;
    background-size: contain;
  }
  &__coin{
    top: 0;
    left: 0;
    height: 43%;
    width: 100%;
    position: absolute;

    i{
      display: block;
      position: absolute;
      max-width: 123px;
      width: 100%;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      img{
        width: 100%;
      }
    }
  }
  &__head{
    padding-left: 13%;
    @include media($md, max){
      padding: 0;
      text-align: center;
    }
  }
  &__wrap{
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 200%;
    overflow: hidden;
  }
}