.wheel{
  max-width: 560px;
  width: 100%;
  position: relative;
  margin: 0 auto;
  @include media($def, max){
       width: 70%;
     }
  @include media($md, max){
    width: 100%;
  }
  &__wrap{
    padding-top: 100%;
    @include media($md, max){
      padding-top: 0;
    }
  }
  &__line{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    //border: 2px solid #91f1b8;
    @include media($md, max){
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      height: initial;
    }

    &:before{
      content: '';
      display: block;
      position: absolute;
      border-radius: 50%;
      border: 2px solid #ffffff;
      width: 80%; 
      height: 80%;
      top: 50%;
      left: 50%;
      transform:translate(-50%,-50%)
    }
    &:after{
      content: '';
      display: block;
      position: absolute;
      border-radius: 50%;
      border: 2px solid #1ce779;
      width: 120%;
      height: 120%;
      top: 50%;
      left: 50%;
      transform:translate(-50%,-50%)
    }
    i{
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__anima{
    width: 168px;
    height: 168px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);
    background: #41e483;
    border-radius: 50%;
  }
  &__an1{
    top: 0%;
    left: -20%;
    position: absolute;
    //transform:translate(-50%,-50%);
  }
  &__an2{
    width: 91px;
    height: 91px;
    top: 39%;
    left: 54%;
    position: absolute;
    //transform:translate(-50%,-50%);
  }
  &__list{
    @include media($md, max){
      height: initial;
    }
  }
  &__item{
    min-height: 105px;
    min-width: 206px;
    max-width: 287px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 28px 15px 21px;
    text-align: center;
    position: absolute;
    border-radius: 15px;
    box-shadow: 8px 8px 1px 0 rgba(0,0,0,0.1);
    @include media($def, max){
      max-width: 235px;
    }
    @include media($md, max){
      position: static;
      transform: translate(0,0);
      margin-bottom: 20px;
    }
    &:nth-child(2n){
      @include media($md, max){
        margin-left:auto;
      }
    }
    &--1{
      left: 50%;
      top: -15%;
      transform: translateX(-50%);
    }
    &--2{
      top: 14%;
      left: 70%;
    }
    &--3{
      top: 60%;
      left: 100%;
      transform: translateX(-50%);
    }
    &--4{
      top: 95%;
      left: 50%;
      transform: translateX(-50%);
    }
    &--5{
      top: 60%;
      left: 0%;
      transform: translateX(-50%);
    }
    &--6{
      top: 14%;
      left: -10%;
    }
  }
  &__num{
    width: 53px;
    height: 53px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    background: url('pic/wheel_num.png') center center no-repeat;
    background-size: 52px 52px;
    span{
      font-size: 25px;
      font-weight: bold;
      color: #fff;
    }
  }
  &__txt{

    font-size: rem(13);
    line-height: rem(16);
  }
}