.footer{
	padding: 63px 0 70px;
	@include media($lg, max){
		padding-top: 0;
	}
	&__contact{
		font-family:$fnt_title;
		@include media($lg, max){
			display: flex;
			flex-wrap: wrap;
		}
	}
	&__item{
		display: flex;
		padding: 15px 0;
		min-height: 70px;
		opacity: 0.5;
		transition: all 0.3s;
		@include media($def, max){
			width: 50%;
		}
		@include media($md, max){
			width: 100%;
		}
		&:hover{
			opacity: 1;
		}
	}
	&__title{
		width: 112px;
		position: relative;
		font-size: 21px;
		color: $c_alt1;
		position: relative;
		&:before{
			content: '';
			display: block;
			width: 700px;
			top: -9px;
			right: 28px;
			position: absolute;
			background: $c_lnk1;
			height: 1px;
			@include media($def, max){
				display: none;
			}
			@include media($md, max){
				display: block;
			}
		}
	}
	&__cnt{
		display: flex;
		flex-direction: column;
	}
	&__link{
		font-size: 13px;
		margin-bottom: 12px;
		&:hover{
			text-decoration: none;
		}

	}
}