.button, .control-holder--text input, .control-holder--text textarea, .control-holder--text select {
  position: relative;
  vertical-align: top;
  box-sizing: border-box;
  font-size: 1rem;
  min-height: 2.25em;
  line-height: 1.75em;
  padding: 0.25em 0.75em;
  border: 1px solid #bfbfbf;
  border-radius: 0.25em;
  background-color: transparent;
  transition: .25s ease;
  text-decoration: none;
  color: inherit;
  max-width: 100%;
}

.button {
  cursor: pointer;
  user-select: none;
  display: inline-block;
  vertical-align: top;
  fill: #807f7f;
  line-height: calc( 1.75em - 3px);
}

.button > span {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.button > span > svg {
  display: inline-block;
  position: relative;
  height: 1.25em;
  width: 1.25em;
  flex-shrink: 0;
  fill: inherit;
}

.button > span > svg:first-child {
  margin-right: 0.5em;
}

.button > span > svg:last-child {
  margin-left: 0.5em;
}

.button > span > svg:only-child {
  margin-left: 0;
  margin-right: 0;
}

.button > span > span {
  display: inline-block;
  text-align: center;
  flex-grow: 1;
  flex-shrink: 1;
}

.button:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 0.25em;
  opacity: 0;
  transition: .25s ease;
}

.button:hover {
  fill: #404040;
  color: #404040;
  background-color: #e6e6e6;
  border-color: #e6e6e6;
}

.button:active {
  transition-duration: .1s;
  transform: translateY(3px);
}

.button:active:before {
  transition-duration: .1s;
  opacity: 1;
}

.button.is-active {
  background-color: #8074ff;
  border-color: #8074ff;
  color: #fff;
  fill: currentColor;
  transform: none !important;
}

.button.is-active:before {
  display: none;
}

.button.is-disabled, .button[disabled] {
  cursor: default;
  pointer-events: none;
  color: #807f7f;
  fill: currentColor;
  border-color: transparent;
  background-color: #e6e6e6;
}

