.invest{
  padding: 50px 0;
  &__head{
    margin-bottom: 42px;
    @include media($md, max){
      text-align: center;
    }
    &-text{
      max-width: 434px;
      width: 100%;
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 15px;
    }
  }

  &__list{
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    display: flex;
    justify-content: center;
    padding: 0 15px;
    align-items: center;
    width: 33.33%;
    position: relative;
    margin-bottom: 20px;
    @include media($def, max){
      width: 100%;
    }
    &--type1{
      .invest__wrap{
        background: #fff url('pic/invest_1.jpg') center center no-repeat;
         @include media($def, max){
           background-size: cover;
        }
      }
    }
    &--type2{
      .invest__wrap{
        background: #fff url('pic/invest_2.jpg') center center no-repeat;
         @include media($def, max){
           background-size: cover;
        }
      }
    }
    &--type3{
      .invest__wrap{
        background: #fff url('pic/invest_3.jpg') center center no-repeat;
         @include media($def, max){
           background-size: cover;
        }
      }
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: #fff;
    min-height: 600px;
    border-radius: 15px;
    padding: 20px 10px;
    text-align: center;
    box-shadow: 0 0 20px rgba(0,0,0,0.2);
    @include media($def, max){
      min-height: initial;
    }
  }
  &__cnt{
    max-width: 360px;
  }
  &__text{
    font-size: 13px;
    line-height: 21px;
    margin-bottom: 28px;
    text-align: center;
  }
}