.step{
  padding-bottom: 80px;
  .view__size{
    position: relative;
  }
  &__line{
    width: 11px;
    background: #e2edff;
    position: absolute;
    left: 15px;
    z-index: 1;
    top: 0;
    @media (max-width:1550px){
      left: 25px;
    }
    @include media($md, max){
      left: 10px !important;
      width: 8px;
    }
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 3px;
    right: 3px;
    background: $c_alt2;
    @include media($md, max){
      left: 2px;
      right: 2px;
    }
  }
  &__item{
    width: 63px;
    height: 63px;
    border: 7px solid #e2edff;
    border-radius:  50%;
    position: absolute;
    z-index: 1;
    left: 50%;
    transform:translateX(-50%);
    @include media($md, max){
      width: 30px;
      height: 30px;
      border: 2px solid #e2edff;
    }
    span{
      background: #fff;
      border-radius:  50%;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: rem(21);
      color: #e2edff;
      font-weight: bold;
      transition: all 0.3s;
      @include media($md, max){
        font-size: 13px;
      }
    }
    &:before{
      display: block;
      opacity: 0;
      transition: all 0.3s;
      content: '';
      width: 5px;
      height: 7px;
      position: absolute;
      left: 50%;
      transform:translateX(-50%);
      bottom: 100%;
      background: $c_alt2;
      @include media($md, max){
        width: 4px;
      }
    }
    &:after{
      display: block;
      opacity: 0;
      transition: all 0.3s;
      content: '';
      width: 5px;
      height: 7px;
      position: absolute;
      left: 50%;
      transform:translateX(-50%);
      top: 100%;
      background: $c_alt2;
      @include media($md, max){
        width: 4px;
      }
    }
    &.has-active{
      span{
        color: #fff;
        background: $c_alt2;
      }
    }
    &--1{
      &.has-active{
        &:after{
          opacity: 1;
        }
      }
    }
    &--2{
      &.has-active{
        &:before{
          opacity: 1;
        }
        &:after{
          opacity: 1;
        }
      }
    }
    &--3{
      &.has-active{
        &:before{
          opacity: 1;
        }
        &:after{
          opacity: 1;
        }
      }
    }
    &--4{
      top: auto;
      bottom: 0;
      &.has-active{
        &:before{
          opacity: 1;
          }
      }
    }
  }

  &__main{
    position: relative;
  }

  &__tunnel{
    @include media($md, max){
      display: none;
    }
    &--type1{
      position: absolute;
      width: 21px;
      left: 54%;
      transform: translateX(-50%);
      top: 100%;
      height: 180px;
      border-left: 2px solid #b4cefb;
      border-right: 2px solid #b4cefb;
      i{
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
      }
      svg{
        height: 100%;
      }
    }
    &--type2{
      position: absolute;
      height: 21px;
      left: 100%;
      transform: translateY(-50%);
      top: 50%;
      width: 126px;
      border-top: 2px solid #b4cefb;
      border-bottom: 2px solid #b4cefb;
      i{
        position: absolute;
        top: 50%;
        height: 140%;
        left: 0;
        transform: translateY(-50%);
      }
      svg{
        width: 100%;
      }
    }
    &--type3{
      position: absolute;
      width: 21px;
      left: 50%;
      transform: translateX(-50%);
      top: 90%;
      height: 210px;
      border-left: 2px solid #b4cefb;
      border-right: 2px solid #b4cefb;
      i{
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
      }
      svg{
        height: 100%;
      }
    }
  }

}





.step1{
  padding-left: 7%;
  padding-bottom: 90px;
  @include media($md, max){
    padding-bottom: 50px;
    padding-left: 25px;
  }
  &__block{
    margin-bottom: 65px;
    position: relative;
    @include media($md, max){
      margin: 20px 0;
    }
    &-wrap{
      position: relative;
      z-index: 1;
      padding: 35px 28px 0;
      box-shadow: 0 0 20px rgba(0,0,0,0.1);
      background: #fff;
    }
    &--type1{
      position: relative;
      top: -42px;
      @include media($md, max){
        top: 0;
      }
    }
  }
  &__wrap{
    padding-right: 12%;
    &--type1{
      padding: 0 12%;
      @include media($md, max){
        padding: 0;
      }
    }
  }
  &__title{
    font-size: rem(21);
    color: $c_txt1;
    font-weight: bold;
    margin-bottom: 7px;
  }
  &__text{
    font-size: rem(13);
    line-height: rem(21);
  }
  &__footer{
    padding-top: 24px;
    padding-bottom: 17px;
    display: flex;
    justify-content: flex-end;
    &--type1{
      padding-top: 15px;
      padding-bottom: 0;
    }
  }

}


.step2{
  padding-left: 7%;
  padding-top: 0px;
  padding-bottom: 0px;
  @include media($md, max){
    padding-left: 25px;
  }
}

.step3{
  padding-left: 7%;
  min-height: 1445px;
  position: relative;
  padding-top: 140px;
  @include media($def, max){
    padding-top: 155px;
  }
  @include media($md, max){
    padding-left: 25px;
    padding-top: 255px;
  }
  &:before{
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    top:0;
    left: 50%;
    width: 2561px;
    height: 1385px;
    transform: translateX(-50%);
    background: url('pic/step3_bg.png') 0 0 no-repeat;
  }
  &__img{
    margin-bottom: 59px;
    &-static{
      display: flex;
      align-items: flex-end;
    }
    &-icon{
      img{
        @include media($md, max){
          height: initial;
        }
      }
    }
    &-list{
      padding-left: 28px;
      display: flex;
      flex-shrink: 0;
    }
    &-item{
      width: 63px;
      display: flex;
      justify-content: center;
      position: relative;
    }
  }
  &__blade{
    position: absolute;
    top: 0;
    left: 50%;
    //margin-left: -33px;
    //margin-top: -39px;
    transform: translate(-50%,-50%);
    i{
      display: block;
      width: 60px;
      height: 60px;
    }
  }
  &__title{
    margin-bottom: 119px;
    display: flex;
    justify-content: flex-end;
    @include media($md, max){
      margin-bottom: 50px;
    }
  }
  &__cnt{
    //padding-left: 15%;
  }
  &__bush1{
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: -4px;
  }
  &__bush2{
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 17px;
  }
}

.step4{
  &__head{
    max-width: 775px;
    padding-left: 8.33%;
    padding-bottom: 210px;
    @include media($lg, max){
      //padding-bottom: 40px;
    }
    @include media($md, max){
      padding-bottom: 0;
      margin-bottom: 60px;
    }
  }
  &__main{
    @include media($lg, max){
      padding-right: 80px;
    }
    @include media($def, max){
      padding-right: 0;
    }
  }
}

