.table{
  position: relative;
  margin-top: 68px;
  margin-bottom: 39px;
  @include media($def, max){
    margin-top: 40px;
  }

  &__bg{
    position: relative;
    padding-top: 10px;
    border-left: 2px solid #f4f4f4;
    border-right: 2px solid #f4f4f4;
    border-bottom: 2px solid #e1e4ec;
    &-line{
      height: 30px;
      border-top: 2px solid #f4f4f4;
    }
  }

  &__cnt{
    padding-top: 15px;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
  &__list{
    display: flex;
    position: relative;
    justify-content: space-between;
    height: 100%;
  }
  &__item{
    width: 12%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: flex-end;
    @include media($sm, max){
      width: 14%;
    }
    &--hot{
      .table__val{
        background: $c_alt1;
      }
    }
  }
  &__column{
    width: 100%;
    height: 100%;
    position: relative;
  }
  &__val{
    height: 100%;
    background: #b4cefc;
  }
  &__price {
    font-size: 16px;
    text-align: center;
    padding: 10px 0;
    color: $c_alt2;
    font-weight: bold;
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 100%;
    @include media($sm, max) {
      font-size: 11px;
    }
  }
  &__date{
    font-size: 13px;
    line-height: 25px;
    width: 100%;
    position: absolute;
    left: 0;
    top: 100%;
    text-align: center;
  }
}