.hist{
  width: 100%;
  margin-bottom: 245px;
  //display: none;
  position: relative;
  @include media($lg, max){
    //width: 90%;
    //margin-left:auto;
    //margin-right: auto;
    //display: none;
  }
  @include media($md, max){
    margin-bottom: 0;
  }
  &__wrap{
    padding-top: 25.78%;

    @include media($md, max){
      padding: 0;
      position: relative;
    }
  }
  &__mobile{
    display: none;
    @include media($md, max){
      display: block;
      left: 50%;
      -webkit-transform: translate(-50%);
      transform: translate(-50%);
      border-right: 2px dashed #19d770;
      top: 0;
      height: 95%;
      position: absolute;
      z-index: -1;
    }
  }
  &__bg{
    position: absolute;
    width: 73%;
    left: 22%;
    top: 0%;
    @include media($md, max){
      display: none;
    }
    svg{
      width: 100%;
    }
  }
  &__list{
    width: 100%;
  }
  &__item{
    position: absolute;
    @include media($md, max){
      position: static;
      width: 100% !important;
      justify-content: center;
      margin-bottom: 20px;
      display: flex;
    }
    .bubbles{
      @include media($md, max){
        transform: scale(1);
        margin: -20px 0;
      }
    }
    &--1{
      top: -33%;
      left: 0;
      width: 50%;
      @media (max-width:1280px){
        top: -19%;
      }
      @media (max-width:1160px){
        top: -11%;
      }
      @media (max-width:1023px){
        top: 0%;
      }
      @media (max-width:905px){
        top: 12%;
      }
    }
    &--2{
      top: -9%;
      left: 71%;
      @media (max-width:1550px){
        transform: scale(0.8);
        left: 68%;
      }
      @media (max-width:1160px){
        left: 65%;
      }
      @media (max-width:1023px){
        top: 7%;
      }
      @media (max-width:855px){
        top: 11%;
        left: 61%;
      }
    }
    &--3 {
      top: -9%;
      left: 89%;
      @media (max-width: 1550px) {
        transform: scale(0.8);
        left: 86%;
      }
      @media (max-width: 1160px) {
        top: -12%;
      }
      @media (max-width: 1023px) {
        top: 7%;
        left: 83%;
      }
      @media (max-width: 855px) {
        top: 10%;
        left: 81%;
      }
    }
    &--4{
      top: 56%;
      left: 89%;
      @media (max-width:1550px){
        transform: scale(0.8);
        left: 86%;
      }
      @media (max-width:1160px){
        top: 61%;
      }
      @media (max-width:1023px){
        top: 70%;
        left: 83%;
      }
      @media (max-width:855px){
        left: 81%;
        top: 75%;
      }

    }
    &--5{
      top: 101%;
      left: 71%;
      @media (max-width:1550px){
        transform: scale(0.8);
        left: 68%;
      }
      @media (max-width:1160px){
        left: 65%;
      }
      @media (max-width:855px){
        left: 62%;
        top: 111%;
      }


    }
    &--6 {
      top: 101%;
      left: 41%;
      @media (max-width: 1160px) {
        top: 122%;
      }
      @media (max-width:1023px){
        top: 123%;
        width: 18%;
      }
      @media (max-width:905px){
        top: 136%;
      }
      @media (max-width: 855px) {
        top: 155%;
      }
    }
    &--7{
      top: 109%;
      left: 0%;
      @media (max-width:1160px){
        top: 122%;
      }
      @media (max-width:1023px){
        top: 132%;
        width: 26%;
      }
      @media (max-width:905px){
        top: 150%;
        width: 26%;
      }

    }
  }

  &__icon{
    text-align: center;
    width: 100%;
    img{
      margin-bottom: rem(15);
      height: auto;
    }
    span{
      display: block;
      font-size: rem(21);
      color: $c_alt2;
      font-weight: bold;
    }
  }
}