.techno{
  padding: 340px 0 175px;
  margin-top: -200px;
  margin-bottom: -100px;
  min-height: 1322px;
  background: url('pic/techno_bg.png') center top no-repeat;
  &__head{
    display: -webkit-box;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 28px;
    @include media($def, max){
      margin-bottom: 10px;
    }
  }
  &__list{
    margin-left: -3%;
    display: flex;
    flex-wrap: wrap;
    margin-right: -3%;
    @include media($def, max){
      margin: 0;
    }
  }
  &__item{
    width: 33.333%;
    margin-bottom: 35px;
    padding: 0 3%;
    @include media($def, max){
      padding: 0;
      margin-bottom: 20px;
      width: 100%;
    }
  }
  &__wrap{
    border-radius: 10px;
    background: #fff;
    box-shadow: 7px 7px 1px 0 rgba(0,0,0,0.1);
    padding: 42px 28px 15px;
    min-height: 490px;
    @include media($def, max){
      min-height: initial;
    }
  }

  &__header{
    min-height: 133px;
    margin-bottom: 28px;
    display: flex;
    justify-content: space-between;
  }
  &__img{
    svg{
      fill: $c_alt1;
      max-height: 133px;
    }
  }
  &__num{
    font-size: 84px;
    position: relative;
    top: -28px;
    line-height: normal;
    color: #e8fbf0;
    font-family: "GT Walsheim Pro", sans-serif;
    @include media($def, max){
      font-size: 66px;
    }
  }
  &__title{
    font-size: rem(21);
    margin-bottom: 15px;
    font-family: $fnt_title;
  }
  &__text{
    font-size: 13px;
    line-height: 21px;
  }
}