.form{
	&__group{
		margin-bottom: 7px;
	}
	&__line{ 
		margin-bottom: 20px;
		textarea,
		select,  
		input{
		    position: relative;
		    vertical-align: top;
		    box-sizing: border-box;
		    font-size: 13px;
		    min-height: 42px;
		    line-height: 1.75em;
		    padding: 0.25em 0.75em;
		    border-radius: none;
			border: none;
			border-bottom: 2px solid #fff;
		    background-color: transparent;
		    -webkit-transition: .25s ease;
		    transition: .25s ease;
		    text-decoration: none;
		    max-width: 100%;
	        display: block;
		    width: 100%;
			color: $c_alt2;
			margin-bottom: 20px;
			font-size: 20px;
			font-weight: bold;
			font-size: 16px;
		    &:focus{ 
		    	outline: none;
		    }
		    &::-webkit-input-placeholder {
				color: #fff;
				font-weight: normal;
			}
			&::-moz-placeholder {
				color: #fff;
				font-weight: normal;
			}
			&:-ms-input-placeholder {
				color: #fff;
				font-weight: normal;
			}
		} 
		textarea{
			height: 70px;
			resize: none;
		}
		&--type2{
			input{
				font-size: 16px;
				height: 42px;
				border-bottom: 2px solid $c_alt1;
				margin-bottom: 0;
				border-bottom: none;
				&::-webkit-input-placeholder {
					color: $c_txt1;
					font-weight: normal;
				}
				&::-moz-placeholder {
					color: $c_txt1;
					font-weight: normal;
				}
				&:-ms-input-placeholder {
					color: $c_txt1;
					font-weight: normal;
				}
			}
		}
		&--m0{
			margin: 0;
		}
	}
	.control-holder{
		label.has-error{ 
			position: absolute;
			padding: 5px 10px;
			right: 0;       
			bottom: -5px;
			border-radius: 5px;
			text-transform: uppercase;
		} 
		&--flag{
			& > label{
				display: inline-block;
			}
		}
		&--file{
			&>label{
				max-width: 300px;
				width: 100%;
			}
			input{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
}    