.view-text{
	h1,h2,h3,h4{
		font-family:$fnt_title;
		color: $c_alt2;
		margin-bottom: 15px;
	}
	h1{
		font-size: rem(40.6);
	}
	h2{
		font-size: rem(33.6);
	}
	h3{
		font-size: rem(21);
	}
	h4{
		font-size: rem(16);
	}
	p{
		font-size: rem(16);
		line-height: rem(24);
		margin-top: 0;
		margin-bottom: 15px;
	}
	blockquote{
		padding: 49px 112px;
		background: #fff;
		box-shadow: 0 0 15px rgba(0,0,0,0.1);
		font-style: italic;
		font-size: 21px;
		line-height: 33px;
		margin-bottom: 15px;
		box-sizing: border-box;
		width: 100%;
		margin-left: 0 !important;
		margin-right: 0 !important;
	}

}