.graph{
  display: flex;
  flex-wrap: wrap;
  padding-top: 21px;
  @include media($sm, max){
    padding-top: 0;
    position: relative;
    top: -60px;
  }
  &__item{
    width: 93px;
    margin-right: 14px;
    height: 507px;
    border-radius: 50px 50px 0 0;
    background: #f6f7f9;
    position: relative;
    @include media($md, max){
      width: 48%;
      margin: 0 1%;
      box-shadow: 0 0 5px rgba(0,0,0,0.3);
    }
    @include media($sm, max){
      height: 455px;
    }
    &:last-child{
      z-index: 1;
    }
  }
  &__cnt{
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    &--type1{
      background: $c_alt1;
      height: 62%;
      .graph__title{
        border-bottom: 2px solid $c_alt1;
      }
    }
    &--type2{
      background: $c_alt2;
      height: 38%;
      .graph__title{
        border-bottom: 2px solid $c_alt2;
      }
    }
  }
  &__total{
    position: absolute;
    width: 100%;
    top: 42px;
    left: 0;
    text-align: center;
    color: #fff;
    font-weight: bold;
    font-size: rem(21);
    z-index: 1;

  }
  &__title{
    position: absolute;
    padding-bottom: 10px;
    width: 346px;
    font-size: rem(21);
    font-weight: bold;
    text-align: right;
    left: 100%;
    top: 21px;
    @include media($def, max){
      width: 285px;
    }
    @include media($md, max){
      left: 0;
      width: 100%;
      top: -50px;
      text-align: center;
      font-size: 12px;
      padding-bottom: 0px;
      border-bottom: none !important;
    }
    @include media($sm, max){
     transform: rotate(-90deg);
      white-space: nowrap;
    }
  }

  &__bubble {
    position: absolute;
    left: 10%;
    right: 10%;
    top: 70px;
    span{
      display: block;
      padding-top: 55.67%;
      background: url('pic/bubble.png') center center no-repeat;
      background-size: contain;
    }
  }
}