body{
	&.has-dim{
		overflow: hidden;
		.view__dim{
			opacity: 1;
			visibility: visible;
		}
	}
}

.view{
	position: relative;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	font-family: $fnt_main;
	font-weight: $f_regular;
	color: $c_txt1;



	&__header{

	}
	&__main{
		flex-grow: 1;
	}
	&__footer{

	}
	&__size{
		max-width: 1350px;
		margin: 0 auto;
		padding: 0 10px;



		//@include media(max, 2000){
		//	padding: 0 2%;
		//}
		@include clearfix;


	}
	&__hidden{
		position: relative;
		padding: 2rem;
		margin: 1.2rem auto;
		background-color: #fff;
		width: 96%;
		max-width: 1024px;
		font-family: $fnt_main;
		font-weight: $f_regular;
		padding: 0;
		//border-radius: 10px;
		//overflow: hidden;
		background: transparent;
		&--lg {
		max-width: 1280px;
		}
		&--def {
			max-width: 947px;
		}

		&--md {
			max-width: 768px;
		}

		&--sm {
			max-width: 480px;
		}
		._show-in--hidden-wrapper {
			display: block;
		}
	}


	&__dim{
		display: flex;
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 10;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.7);
		opacity: 0;
		visibility: hidden;
	}


	&__section{
		padding: 20px 0;
	}
}








