.circle{
  max-width: 600px;
  width: 80%;
  margin: 0 auto;
  @media (max-width:1550px){
    transform: scale(0.8);
  }
  @include media($lg, max){
    transform: scale(0.7) !important;
  }
  @include media($md, max){
    display: none;
  }
  &__wrap{
    padding-top: 100%;
    position: relative;
  }
  &__cnt{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__list{
    height: 100%;
    border-radius: 50%;
    position: relative;
    z-index: 1;
    //border: 3px dashed $c_alt1;
    @include media($def, max){
      left: -40px;
    }
  }
  &__item{
    position: absolute;
    z-index: 1;
    max-width: 154px;
    width: 40%;
    &--type1{
      left: 50%;
      top: 0;
      transform:translate(-50%,-50%);
    }
    &--type2{
      top: 50%;
      right: 0;
      transform:translate(50%,-50%);
    }
    &--type3{
      left: 50%;
      bottom: 0;
      transform:translate(-50%,50%);
    }
    &--type4{
      left: 0;
      top: 50%;
      transform:translate(-50%,-50%);
    }
  }

  &__line{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
}