////
/// @group _mixins/grid.scss
////

///    
/// @type Boolean
$grid__in-rems: false !default;

///   () 
/// @type Number
$grid__columns-count: 12 !default;

///    
/// @type String
$grid__grid-class: 'grid' !default;

///     ()
/// @type String
$grid__cell-class: 'gcell' !default;

/// ???????????????
/// @type String
$grid__box-class: 'gbox' !default;

/// @type Size
$grid__gutter: $gutter;

/// @type Map
$grid__gutter-factor: $gutter-factor;

/// @type String
$grid__delimiter: $delimiter;

/// @type Map
$grid__breakpoints: $breakpoints;

@if $grid__in-rems {
	$grid__gutter: rem($gutter);
}

///   flex 
///
/// @param {Size|Keyword} $width [auto] -  
/// @param {Size|Keyword} $rangeWidth [$width] - /  
/// @param {Size|Keyword} $flexBasis [$width] - /  
/// @param {Keyword} $dir [max] - /  
@mixin flexBasis( $width:auto, $rangeWidth:$width, $flexBasis:$width, $dir:max ) {
	width: $width;
	@if ($dir == max) {
		max-width: $rangeWidth;
	} @else {
		min-width: $rangeWidth;
	}
	flex-basis: $flexBasis;
}

///   flex  c  
///
/// @param {Size|Keyword} $width [auto] -  
/// @param {Size|Keyword} $rangeWidth [$width] - /  
/// @param {Size|Keyword} $flexBasis [$width] - /  
@mixin flexBasisMin( $width:auto, $rangeWidth:$width, $flexBasis:$width ) {
	@include flexBasis( $width, $rangeWidth, $flexBasis, min);
}


/// @access private
@mixin _grid-spaces($cell_class, $point-alias: '') {
	@each $factor-name, $factor in $grid__gutter-factor {
		&--#{$point-alias}space#{$factor-name} {
			margin-left: ($gutter*$factor)/-2;
			margin-right: ($gutter*$factor)/-2;
			> .#{$cell_class} {
				padding-left: ($gutter*$factor)/2;
				padding-right: ($gutter*$factor)/2;
			}
		}
	}
}


/// @access private
@mixin _grid-columns($cell_class, $columns, $point-alias: '') {
	@for $i from 1 through $columns {
		&--#{$point-alias}#{$i} {
			>.#{$cell_class} {
				@include flexBasis( 100% / $i );
			}
		}
	}
}


/// @access private
@mixin _grid-cell($columns, $point-alias: '') {
	@for $i from 1 through $columns {
		&--#{$point-alias}#{$i} {
			@include flexBasis( ($i * 100%) / $columns );
		}
	}
}


///   
@mixin grid(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {

	.#{$grid_class} {
		display: flex;
		flex-wrap: wrap;


		.#{$box_class} {
			height: 100%;
			min-height: 100%;
		}

		// : grid--5
		@include _grid-columns($cell_class, $columns);
		@include _grid-spaces($cell_class);


		> .#{$cell_class} {
			display: inline-block;
			@include _grid-cell($columns);
		}
	}
}


///      
@mixin grid-mq(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {

	.#{$grid_class} {
		//   
		@each $point-name, $point in $breakpoints {
			//  
			@include media($point-name) {

				// : md-
				$point-alias: $point-name + $delimiter;

				// : grid--md-5
				@include _grid-columns($cell_class, $columns, $point-alias);

				// : grid--md-auto
				&--#{$point-alias}auto {
					> .#{$cell_class} {
						@include flexBasis( auto, none );
					}
				}

				// : .grid > .gcell
				> .#{$cell_class} {
					@include _grid-cell($columns, $point-alias);

					&--#{$point-alias}auto {
						@include flexBasis( auto, 100% );
					}
				}
			}
		}
	}
}


///        
@mixin grid-spaces-mq(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {

	.#{$grid_class} {
		//   
		@each $point-name, $point in $breakpoints {
			//  
			@include media($point-name) {

				// : md-
				$point-alias: $point-name + $delimiter;

				@include _grid-spaces($cell_class, $point-alias);

				// : grid--lg-auto
				&--#{$point-alias}nospace {
					margin: 0;
					> .#{$cell_class} {
						padding: 0;
					}
				}
			}
		}
	}
}


///   ,    
@mixin grid-orders(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {

	.#{$grid_class} {

		&--inline {
			display: inline-flex;
		}
		&--items-start {
			align-items: flex-start;
		}
		&--items-end {
			align-items: flex-end;
		}
		&--items-center {
			align-items: center;
		}
		&--items-stretch {
			align-items: stretch;
		}
		&--justify-start {
			justify-content: flex-start;
		}
		&--justify-end {
			justify-content: flex-end;
		}
		&--justify-center {
			justify-content: center;
		}
		&--justify-between {
			justify-content: space-between;
		}
		&--justify-around {
			justify-content: space-around;
		}
		&--nowrap {
			flex-wrap: nowrap;
		}
		&--column {
			flex-direction: column;
		}
		&--row {
			flex-direction: row;
		}
		&--row-reverse {
			flex-direction: row-reverse;
		}


		> .#{$cell_class} {

			&--start {
				align-self: flex-start;
			}
			&--end {
				align-self: flex-end;
			}
			&--center {
				align-self: center;
			}
			&--stretch {
				align-self: stretch;
			}
			&--nogrow {
				flex-grow: 0;
			}
			&--grow {
				flex-grow: 1;
			}
			&--shrink {
				flex-shrink: 1;
			}
			&--noshrink{
				flex-shrink: 0;
			}
			&--order-start {
				order: -1;
			}
			&--order-end {
				order: 1;
			}
			&--noorder {
				order: 0;
			}
			&--nobasis {
				flex-basis: 0;
			}
		}

	}

}



///      ,    
@mixin grid-orders-mq(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {

	.#{$grid_class} {
		//   
		@each $point-name, $point in $breakpoints {
			//  
			@include media($point-name) {

				// : md-
				$point-alias: $point-name + $delimiter;

				//  
				&--#{$point-alias}items-start {
					align-items: flex-start;
				}
				&--#{$point-alias}items-end {
					align-items: flex-end;
				}
				&--#{$point-alias}items-center {
					align-items: center;
				}
				&--#{$point-alias}items-stretch {
					align-items: stretch;
				}
				&--#{$point-alias}justify-start {
					justify-content: flex-start;
				}
				&--#{$point-alias}justify-end {
					justify-content: flex-end;
				}
				&--#{$point-alias}justify-center {
					justify-content: center;
				}
				&--#{$point-alias}justify-between {
					justify-content: space-between;
				}
				&--#{$point-alias}justify-around {
					justify-content: space-around;
				}
				&--#{$point-alias}nowrap {
					flex-wrap: nowrap;
				}
				&--#{$point-alias}wrap {
					flex-wrap: wrap;
				}
				&--#{$point-alias}column {
					flex-direction: column;
				}
				&--#{$point-alias}row {
					flex-direction: row;
				}
				&--#{$point-alias}row-reverse {
					flex-direction: row-reverse;
				}

				> .#{$cell_class} {
					&--#{$point-alias}start {
						align-self: flex-start;
					}
					&--#{$point-alias}end {
						align-self: flex-end;
					}
					&--#{$point-alias}center {
						align-self: center;
					}
					&--#{$point-alias}stretch {
						align-self: stretch;
					}
					&--#{$point-alias}nogrow {
						flex-grow: 0;
					}
					&--#{$point-alias}grow {
						flex-grow: 1;
					}
					&--#{$point-alias}shrink {
						flex-shrink: 1;
					}
					&--#{$point-alias}noshrink{
						flex-shrink: 0;
					}
					&--#{$point-alias}order-start {
						order: -1;
					}
					&--#{$point-alias}order-end {
						order: 1;
					}
					&--#{$point-alias}noorder {
						order: 0;
					}
					&--#{$point-alias}nobasis {
						flex-basis: 0;
					}
				}
			}

		}
	}



	.grid>.gcell--parser-half {
		@include flexBasis(360px);
	}
}





///     
@mixin grid-full(
	$columns: $grid__columns-count,
	$grid_class: $grid__grid-class,
	$cell_class: $grid__cell-class,
	$box_class: $grid__box-class
) {
	@include grid;
	@include grid-mq;
	@include grid-orders;
	@include grid-orders-mq;
	@include grid-spaces-mq;
}