.fact{
  //background: url('pic/fact__bg.png') right top no-repeat;
  padding: 35px 0;
  &__head{
    margin-bottom: 42px;
    @include media($sm, max){
      margin-bottom: 95px;
    }
  }
  &__cnt{
    max-height: 255px;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    @include media($md, max){
      text-align: center;
    }
    &--bg{
      &:before{
        content: '';
        display: block;
        width: 1391px;
        height: 844px;
        position: absolute;
        z-index: -1;
        left: -15%;
        top: 62%;
        transform: translateY(-50%);
        background: url('pic/fact__bg.png') center  center no-repeat;
        @include media($sm, max){
          left: -65%;
        }
      }
    }
    &--full{
      max-height: initial;
    }
    &--light{
      .view-text{
        & > *{
          color: #fff;
        }
      }
    }
  }
}
