.menu{
	//font-family: $fnt_title;
	&__list {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		justify-content: space-between;
		margin: 0;
		padding: 0;
		list-style: none;
		box-sizing: border-box;
		& > li {
			flex-grow: 1;
			position: relative;
			padding: 0px 5px;
			text-align: center;
			justify-content: center;
			align-items: center;
			display: flex;
			@include media($def, max) {
				width: 100%;
				max-width: 100%;
				padding: 10px 20px;
				position: static;
				display: flex;
				justify-content: space-between;

			}
			&:hover {
				& > ul {
					opacity: 1;
					visibility: visible;
				}
			}
			& > a {
				color: #fff;
				font-size: 13px;
				text-decoration: none;
				transition: all 0.3s;
				position: relative;
				@include media($def, max) {
					 color: $c_alt1;
				 }
				&:hover {
					color: $c_alt2;
					@include media($def, max) {
						color: $c_alt1;
					}
				}
				&:before{
					content: '';
					width: 0;
					bottom: -15px;
					left: 0;
					height: 2px;
					background: #fff;
					position: absolute;
					transition: all 0.3s;
				}
				&.has-active{
					&:before{
						width: 100%;
					}
				}
			}
		}
	}
}