////
/// @group _partials/buttons-default.scss
////


// vars
// ================================

/// @type Size
$button-border-radius: .25em !default;

/// ,       
/// @type Size
$button-gap: .25em !default;

///     
/// @type Map
$button-sizes: (
	sm: 12px,
	md: 14px,
	lg: 18px,
	xl: 20px
);


// mixins
// ================================

///  
///
/// @param {Color} $bg-color -  
/// @param {Color} $text-color [#000] -  
/// @param {Number} $lighten [10%] -   
@mixin color-states( $bg-color, $text-color:#000, $lighten:10%, $shadow: $lighten * 2  ) {
	background-color: $bg-color;
	border-color: $bg-color;
	color: $text-color;
	fill: currentColor;

	&:hover {
		color: lighten( $text-color, $lighten );
		fill: lighten( $text-color, $lighten );
		background-color: lighten( $bg-color, $lighten / 2 );
		border-color: $bg-color;
	}

	&-o {
		@include color-states-o( $bg-color, $text-color, $lighten, $shadow );
	}
}

///  ,        
@mixin color-states-o( $bg-color, $text-color:#000, $lighten:10%, $shadow: $lighten * 2  ) {
	border-color: $bg-color;
	color: $bg-color;
	fill: currentColor;

	&:hover {
		color: lighten( $text-color, $lighten );
		fill: lighten( $text-color, $lighten );
		background-color: lighten( $bg-color, $lighten / 2 );
		border-color: $bg-color;
	}
}



// placeholder
// ================================
%buttonsGeometry {
	position: relative;
	vertical-align: top;
	box-sizing: border-box;
	font-size: 1rem;
	min-height: ($button-gap * 9);
	line-height: ($button-gap * 7);
	padding: $button-gap ($button-gap * 3);
	border: 1px solid $color-gray25;
	border-radius: $button-border-radius;
	background-color: transparent;
	transition: .25s ease;
	text-decoration: none;
	color: inherit;
	max-width: 100%;
}



// scss
// ================================

//   
.button {
	@extend %buttonsGeometry;
	// outline: none;
	cursor: pointer;
	user-select: none;
	display: inline-block;
	vertical-align: top;
	fill: $color-gray50;
	line-height: calc( #{($button-gap * 7)} - 3px );

	& > span {
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-around;




		& > svg {
			display: inline-block;
			position: relative;
			height: ($button-gap * 5);
			width: ($button-gap * 5);
			flex-shrink: 0;
			fill:inherit;

			&:first-child {
				margin-right: ($button-gap * 2);
			}

			&:last-child {
				margin-left: ($button-gap * 2);
			}

			&:only-child {
				margin-left: 0;
				margin-right: 0;
			}

		}

		& > span {
			display: inline-block;
			text-align: center;
			flex-grow: 1;
			flex-shrink: 1;
		}
	}


// .button:hover .button:active
// =========================================

	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		background-color: rgba( #000, .2 );
		border-radius: $button-border-radius;
		opacity: 0;
		transition: .25s ease;
	}

	&:hover {
		fill: $color-gray75;
		color: $color-gray75;
		background-color: $color-gray10;
		border-color: $color-gray10;
	}

	&:active {
		transition-duration: .1s;
		transform: translateY(3px);

		&:before {
			transition-duration: .1s;
			opacity: 1;
		}
	}


// 
// =========================================

	&.is-active {
		background-color: $color-primary;
		border-color: $color-primary;
		color: #fff;
		fill: currentColor;
		transform: none !important;
		&:before {
			display: none;
		}
	}

	&.is-disabled,
	&[disabled] {
		cursor: default;
		pointer-events: none;
		color: $color-gray50;
		fill: currentColor;
		border-color: transparent;
		background-color: $color-gray10;
	}


//     
// =========================================

	/// @access private
	@mixin onlyicon {
		> span {
			svg {
				margin-left: 0 !important;
				margin-right: 0 !important;
			}
			> span {
				display: none;
			}
		}
	}

	&--only-icon {
		@include onlyicon;
	}

	@each $point-name, $point in $breakpoints {

		@include media($point-name, max) {
			$point-alias: $point-name + $delimiter;

			&--#{$point-alias}only-icon {
				@include onlyicon;
			}
		}
	}


//  
// =========================================

	&--primary {
		@include color-states( $color-primary, #fff );
	}

	&--secondary {
		@include color-states( $color-secondary, #fff );
	}

	&--success {
		@include color-states( $color-success, #fff );
	}

	&--warning {
		@include color-states( $color-warning, #fff );
	}

	&--danger {
		@include color-states( $color-danger, #fff );
	}

	&--info {
		@include color-states( $color-info, #fff );
	}


// 
// =========================================

	@each $point-name, $point in $button-sizes {

		$point-size: $point-name + $delimiter + 'size';
		&--#{$point-size} {
			font-size: $point;
		}
	}

	&--expand-element,
	&--expand-text {
		display: block;
		width: 100%;

		& + .button {
			margin-top: $button-gap;
		}
	}

	&--expand-element {
		text-align: center;
		> span {
			display: inline-flex;
    		vertical-align: middle;
			width: auto;
			margin-left: auto;
			margin-right: auto;
		}
	}
}


//    
.buttons-group {
	display: flex;

	&--start {
		justify-content: flex-start;
	}

	&--center {
		justify-content: center;
	}

	&--end {
		justify-content: flex-end;
	}

	.button {
		border-radius: 0;
		border-left-width: 0;

		&:first-child {
			border-left-width: 1px;
			border-top-left-radius: $button-border-radius;
			border-bottom-left-radius: $button-border-radius;
		}

		&:last-child {
			border-top-right-radius: $button-border-radius;
			border-bottom-right-radius: $button-border-radius;
		}

		&--disabled,
		&[disabled] {

			+ .button {
				border-left-width: 1px;
			}
		}
	}

	@each $point-name, $point in $button-sizes {

		$point-size: $point-name + $delimiter + 'size';
		&--#{$point-size} .button {
			font-size: $point;
		}
	}
}



////
/// @group _partials/content-image.scss
////


// mixins
// ================================

@mixin content-image-full-width {
	display: block;
	float: none !important;
	margin-left: auto !important;
	margin-right: auto !important;
};


// scss
// ================================

.content-image {
	position: relative;

	&--width-1200-and-more {
		@include content-image-full-width;
	}

	&--width-600-and-more {
		.view-text--article & {
			@include content-image-full-width;
		}
	}
}

.content-image {
	$and-more-sizes: (
		$mg: 1000,
		$xl: 900,
		$lg: 800,
		$md: 500,
		$ms: 300,
		$sm: 200,
		$xs: 100
	);

	@each $size-mq, $size-value in $and-more-sizes {
		@include media( $size-mq, max ) {
			&--width-#{$size-value}-and-more {
				@include content-image-full-width;
			}
		}
	}
}

////
/// @group _partials/sitemap.scss
////


// vars
// ================================

/// @type Color
$sitemap-hoverbg: rgba( #000, .035 ) !default;

// scss
// ================================

//  
.sitemap {
	max-width: $md;
	padding: 1rem;
	background-color: #f6f7f9;
	position: relative;
	font-size: 1rem;
	margin-bottom: 1.5rem;

	@include media( $md ) {
		padding-left: 2rem;
		padding-right: 2rem;
	}

	ul {
		list-style: none;
		padding: 0 0 0 2em;
		margin: 0;
	}

	li {
		list-style: none;
		line-height: 1.4em;
	}

	a {
		padding: .2em 1em .2em .5em;
		display: block;
		color: #00e;

		&:visited {
			color: #0e00ad;
		}

		&:hover {
			color: #f34;
			background-color: $sitemap-hoverbg;
		}

		&:active {
			color: #000;
			background-color: $sitemap-hoverbg;
		}
	}
}

//   
.sitemap > ul {
	padding: 0;
	margin: 1em 0;
	position: relative;
	overflow: hidden;
}

//  
//  
.sitemap > ul > li > ul {
	padding-left: 0;
}

//   
.sitemap > ul ul {
	list-style: none;
	padding: 0 0 0 2em;
	margin: 0;
}

//  
//     
.sitemap > ul ul li {

	position: relative;
	margin: .1em 0;

	&:hover {
		background-color: $sitemap-hoverbg;
	}

	a {
		position: relative;
		padding-left: 2.5em;

		&:before,
		&:after {
			content: '';
			position: absolute;
			top: .2em;
			width: 1600px;
			height: 1.4em;
			text-align: right;
			opacity: .3;
			background: transparent repeat-x center right;
		}

		&:before {
			left: 0;
			width: 2em;
			background-image: url(pic/sitemap-start.svg);
		}

		&:after {
			right: 100%;
			background-image: url(pic/sitemap-middle.svg);
		}

	}
}

//   
.sitemap > ul ul li {
	&:last-child {
		> a:only-child {
			&:before {
				background-image: url(pic/sitemap-end.svg);
			}
		}
	}
}




.breadcrumbs {
	width: 100%;
	display: table;
	font-size: 0;
	line-height: 0;
	margin: 1rem 0;
	font-size: 1rem;
}

.breadcrumbs a {
	color: #f00;
	text-decoration: underline;
}

.breadcrumbs span {
	color: #000;
	display: inline-block;
	vertical-align: top;
	font-size: 1em;
	line-height: 1.4;
	margin-right: .5em;

	&:before {
		margin-right: .5em;
		content: '/'; 
		display: inline-block;
		vertical-align: top;
	}

	&:first-child:before {
		display: none;
	}
}

.breadcrumbs {
	@include media( $md, max ) {
		display: block;

		span {
			&, &:before {
				display: none;
			}

			&:nth-last-child(2) {
				display: block;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				max-width: 100%;
				background: none;
				margin: 0;

				&:before {
					content: '<';
					margin: 0 5px 0 3px;
					display: inline-block;
				}
			}
		}
	}
}


////
/// @group _partials/view-text.scss
////


// scss
// ================================

.view-text {
	color: inherit;
	position: relative;
	font-size: 1rem;
	line-height: 1.5;

	blockquote, & {
		> :first-child {
			margin-top: 0;
		}

		> :last-child {
			margin-bottom: 0;
		}
	}
}

// 
.view-text {
	h1, h2, h3, h4, h5, h6 {
		clear: both;
		line-height: 1.25;
		font-weight: bold;
		margin: 0.6em 0 1rem;
	}

	h1 {
		font-size: 2.5em;
	}

	h2 {
		font-size: 2.25em;
	}

	h3 {
		font-size: 1.75em;
	}

	h4 {
		font-size: 1.5em;
	}

	h5 {
		font-size: 1.25em;
	}

	h6 {
		font-size: 1em;
	}
}

//  
.view-text {
	p, ul, ol, hr, dl, pre, address, blockquote, table,
	.table-wrapper, .media-wrapper {
		margin: 1em 0;
	}

	blockquote {
		padding: 1em 1.5em;
		background-color: #f7f8e2;
	}

	hr {
		clear: both;
		border-width: 0;
		border-top-width: 1px;
	}
}

// 
.view-text {
	ul, ol {
		padding-left: 1.5em;
	}

	dd {
		margin-left: 1.5em;
	}

	dt {
		font-weight: bold;
	}

	dd + dt {
		margin: 1em 0 0;
	}
}

//  
.view-text {
	pre, code, kbd, samp {
		font-style: normal;
		font-weight: normal;
		font-family: $font-family--monospace;
	}

	pre {
		tab-size: 4;
		padding: 1em 1.5em;
		background-color: #f5f5f5;
	}

	code, kbd, samp {
		padding: 0 .4em;
	}

	code {
		background-color: #f5f5f5;
	}

	kbd {
		background-color: #e1f0fa;
	}

	samp {
		background-color: #f7f8e2;
	}
}

// media 
.view-text {
	iframe {
		border-width: 0;
	}

	audio {
		width: 100%;
	}

	audio[controls] {
		display: block;
	}

	.media-wrapper {
		&__holder {
			background-color: #f5f5f5;
			position: relative;
			iframe, video {
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
			}
		}
	}
}

// 
.view-text {
	table  {
		width: 100%;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
		border: 1px solid #ddd;
	}

	caption {
		margin-bottom: .5em;
		caption-side: top;
	}

	tbody, tfoot, thead, tr, th, td {
		border: inherit;
	}

	th, td {
	   padding: .5em;
	   text-align: left;
	   vertical-align: top;
	}

	th {
		font-weight: bold;
		text-align: center;
		background-color: #f5f5f5;
	}
}

//  " "
.view-text .table-null {
	&, th, td {
		border-color: transparent;
	}

	th {
		background: transparent;
	}
}

//  ""
.view-text .table-zebra {
	&, th, td {
		border-color: transparent;
	}

	th {
		background: transparent;
	}

	tr:nth-child(odd) {
		background-color: #f5f5f5;
	}
}

// 
.view-text {
	a {
		color: #43f;
		&:visited {
			color: #0e00ad;
		}
		&:hover {
			color: #f34;
		}
		&:active {
			color: #000;
		}
	}
}

//   
.view-text {
	b, strong {
		font-weight: bold;
	}

	i, cite, em, var, address, dfn, caption {
		font-style: italic;
	}

	abbr[title], dfn[title] {
		text-decoration: none;
		border-bottom: 1px dotted;
		cursor: help;
	}
}

//    
//      .view-size
.view-text {
	@include media( $def, max ) {
		> blockquote,
		> pre,
		> .media-wrapper,
		> video,
		> iframe
		{
			margin-left: -1.5rem;
			margin-right: -1.5rem;
		}

		> blockquote,
		> pre
		{
			padding-left: 1.5rem;
			padding-right: 1.5rem;
		}
	}
}

//     
.view-text--article {
	> p,
	> h1,
	> h2,
	> h3,
	> h4,
	> h5,
	> h6,
	//> blockquote,
	//> pre,
	> hr,
	> ul,
	> ol,
	> dl,
	> audio,
	> address
	{
		max-width: $def;
		margin-left: auto;
		margin-right: auto;
	}
}



///     
$contentClass: '.view__hidden';

/// Fade-zoom animation
/// @link http://dimsemenov.com/plugins/magnific-popup/
.zoom-in {
	#{$contentClass} {
		opacity: 0;
		transition: all 0.3s ease-in-out;
		transform: scale(0.8);
	}

	/* animate in */
	&.mfp-ready #{$contentClass} {
		opacity: 1;
		transform: scale(1);
	}

	/* animate out */
	&.mfp-removing #{$contentClass} {
		transform: scale(0.8);
		opacity: 0;
	} 

	/* Dark overlay, start state */
	&.mfp-bg {
		opacity: 0;
		transition: opacity 0.3s ease-out;
	}
	/* animate in */
	&.mfp-ready.mfp-bg {
		opacity: 0.8;
	}
	/* animate out */
	&.mfp-removing.mfp-bg {
		opacity: 0;
	}
}



////
/// @group _partials/form.scss
////


// scss
// ================================

.form {
	position: relative;
	font-size: 1rem;

	&__group {
		display: block;
		position: relative;
		margin: 0 0 1.6rem;
		& & {
			margin: 0 0 .8rem;
		}
	}

	&__label {
		cursor: default;
		display: inline-block;
		vertical-align: top;
		user-select: none;
		margin-bottom: .4rem;
	}

	&__caption {
		font-size: .8em;
		margin-bottom: .4em;
	}

	&__info {
		display: none;
		pointer-events: none;
		position: absolute;
		top: 0;
		right: 0;
		font-size: .7em;
		background-color: #000;
		color: #fff;
		padding: .1em .3em;
		input:focus ~ &,
		textarea:focus ~ &,
		select:focus ~ & {
			display: block;
		}
	}
}

label.form__label {
	cursor: pointer;
}


.control-holder {
	position: relative;

	label.has-error {
		display: inline-block;
		vertical-align: top;
		user-select: none;
		font-size: .75em;
		line-height: 1em;
		padding: .1em .5em;
		background-color: #000;
		color: #fff;
	}

	&--text {
		input, textarea, select {
			@extend %buttonsGeometry;
			display: block;
			width: 100%;
			line-height: calc( #{($button-gap * 7)} - 2px );
		}
		textarea {
			resize: vertical;
			min-height: 7em;
			max-height: 18em;
		}

		@each $point-name, $point in $button-sizes {

			$point-size: $point-name + $delimiter + 'size';
			&-#{$point-size} {
				input, textarea, select {
					font-size: $point;
				}
			}
		}
	}

	&--flag {
		label {
			user-select: none;
			margin-right: 1em;
			cursor: pointer;
			position: relative;
			z-index: 1;
			&:only-child,
			&.has-error {
				cursor: default;
				margin-right: 0;
			}
		}
		ins {
			position: relative;
			display: inline-block;
			vertical-align: text-bottom;
			width: 1.2em;
			height: 1.2em;
			background-color: #fff;
			border: 1px solid;
		}
		input {
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 0;
			opacity: 0;
			z-index: -1;
			&[type="radio"] {
				~ ins {
					border-radius: 50%;
				}
			}
			&:checked {
				~ ins {
					background-color: #000;
					box-shadow: inset 0 0 0 3px #fff;
				}
			}
			&:disabled {
				~ ins, 
				~ span {
					opacity: .5; 
					cursor: not-allowed;
				}
			} 
		}
	}
 
	&--file { 
		label {   
			position: relative; 
			display: inline-block;  
			vertical-align: top;
		}  

		input {
			position: absolute;
			outline: none;
			top: 0;
			left: 0;
			width: 0;
			height: 0; 
			opacity: 0; 
		}
 
		._ellipsis {
			max-width: 200px;
			width: auto;
			display: inline-block;
			vertical-align: top;
		}
	}
}
.ui{ 
	&__form{
		padding: 100px 0;
		max-width: 600px;
		margin: 0 auto;
	}
}

#fPopUp{
	@include media($sm, max){
		 min-width: 100%;
	    left: 10px;
	    right: 10px;
	}
}

