.speedo{
  display: flex;
  flex-wrap: wrap;
  @include media($md, max){
    align-items: center;
  }
  &__item{
    width: 50%;
    &--type1{
      .speedo__main{
        max-width: 175px;
      }
    }
    &--type2{
      padding-top: 119px;
      @include media($md, max){
        padding-top: 0;
      }
      .speedo__main{
        max-width: 245px;
      }
      .speedo__date{
        font-size: rem(24);
      }
      .speedo__title{
        font-size: rem(24);
      }
    }

  }

  .js-speedoArr1 i{
    transform: rotate(115deg)
  }
  .js-speedoArr2 i{
    transform: rotate(115deg)
  }

  &__wrap{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
  }
  &__main{
    width: 100%;
    position: relative;
    img{
      width: 100%;
    }
  }
  &__arrow{
    position: absolute;
    width: 95%;
    height: 95%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    i{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &__text{
    text-align: center;
    position: relative;
    top: -50px;
    width: 100%;
    @include media($md, max){
      top: -30px;
    }
  }
  &__date{
    font-size: rem(17);
    margin-bottom: 10px;
    color: $c_alt1;
    @include media($md, max){
      margin-bottom: 5px;
    }
  }
  &__title{
    font-size: rem(17);
    color: $c_alt2;
    font-weight: bold;
  }
}