.brick{
  @include media($def, max){
    min-height: 250px;
    max-width: 630px;
    margin: 0 auto;
  }
  &__table{
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 189px;
    @include media($def, max){
      top: 50px;
    }
  }
  &__column{
    padding: 0 10px;
    width: 19%;
    display: flex;
    align-items: flex-end;
    @include media($md, max){
      //padding: 0 3px;
    }
    &--type1{
      .brick__wrap{
        bottom: 0%;
      }
    }
    &--type2{
      .brick__wrap{
        bottom: 30%;
        @include media($def, max){
          bottom: 10%;
        }
      }
    }
    &--type3{
      .brick__wrap{
        bottom: 60%;
        @include media($def, max){
          bottom: 20%;
        }
      }
    }
    &--type4{
      .brick__wrap{
        bottom: 90%;
        @include media($def, max){
          bottom: 30%;
        }
      }
    }
    &--type5{
      .brick__wrap{
        bottom: 120%;
        @include media($def, max){
          bottom: 40%;
        }
      }
    }
  }
  &__wrap{
    position: relative;
  }
  &__text{
    padding-right: 15px;
    position: absolute;
    right: 100%;
    top: 42px;
    text-align: right;
    @media(max-width: 1550px){
      width: 100%;
      bottom: 100%;
      top: auto;
      left: 0;
    }
    @include media($def, max){
      padding-right: 5px;
    }
  }
  &__price{
    font-size: 16px;
    color: $c_alt2;
    margin-bottom: 5px;
    font-weight: bold;
  }
  &__title{
    font-size: 13px;
    white-space: nowrap;
    @media(max-width: 1550px){
      white-space: initial;
    }
    @include media($def, max){
      font-size: 11px;
    }
  }
  &__cnt{
    display: flex;
    flex-wrap: wrap;
    width: 91px;
    @media(max-width: 510px){
      width: 70px;
    }
    @media(max-width: 390px){
      width: 52px;
    }
  }
  &__item{
    margin-bottom: -32px;
    position: relative;
    width: 100%;
    top: 0;
    @media(max-width: 510px){
      margin-bottom: -24px;
    }
    @media(max-width: 390px){
      margin-bottom: -18px;
    }
    &:nth-child(1){
      order: 5
    }
    &:nth-child(2){
      order: 4
    }
    &:nth-child(3){
      order: 3
    }
    &:nth-child(4){
      order: 2
    }
    &:nth-child(5){
      order: 1
    }
    img{
      width: 100%;
      height: 100%;
    }
  }
}