.header{
	position: absolute;
	width: 100%;
	padding: 28px 0;
	@include media($def, min) {
        display: block;
	}
	&.has-active{
		background: #fff;
		box-shadow: 0 0 10px rgba(0,0,0,0.5);
		padding: 25px 0;
		z-index: 5;
		top: 0 !important;
		.header__line{
			display: block;
		}
		.menu__list > li > a{
			color: $c_alt1;
			&:hover{
				color: $c_alt2;
			}
			&.has-active{
				color: $c_alt2;
				&:before{
					display: none;
				}
			}
		}
		.mobile__button{
			span{
				background: $c_alt1;
				&:before{
					background: $c_alt1;
				}
				&:after{
					background: $c_alt1;
				}
			}
		}

		.downList{
			border-radius: 20px;
			padding: 2px 10px;
			border: 2px solid $c_alt2;
			&__head{
				&:before{
					border-top: 1px solid $c_alt2;
					border-right: 1px solid $c_alt2;
				}
			}
			&__name{
				color: $c_alt2;

			}
			&.has-active{
				.downList__head{
					border-bottom: none;
				}
				.downList__content{
					//top: 100%;
				}

			}
		}

	}
	&__wrap{
		display: flex;
		align-items: center;
		justify-content: space-between;

	}
	&__logo {
		max-width: 440px;
		width: 100%;
		flex-shrink: 1;
		@include media($lg, max) {
			max-width: 250px;
		}
		@media only screen and (max-width: 1111px){
			max-width: 200px;
		}
		&-link{
			max-width: 192px;
			display: inline-block;
			transition: all 0.3s;
			&:hover{
				opacity: 0.8;
			}
		}
	}

	&__right{
		display: flex;
		align-items: center;
		flex-grow: 1;
		@include media($def, max) {
			justify-content: flex-end;
		}
	}
	&__menu{
		flex-grow: 1;
		position: relative;
		@include media($def,max){
			order:3;
			flex-grow: initial;
		}
		&-content{
			@include media($def,max){
				display: none;
				position: absolute;
				padding: 10px 0;
				top: 100%;
				right: 0;
				width: 280px;
				background: #fff;
				z-index: 2;
				border-radius: 15px;
				box-shadow: 0 0 15px rgba(0,0,0,0.2);
			}
		}
	}

	&__language{
		@include media($def,max){
			order:2;
		}
	}


	&__line{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 2px;
		display: none;
		span{
			display: block;
			height: 100%;
			background: $c_alt1;
		}
	}


}