.map{
  padding: 100px 0 70px;
  @include media($def, max){
    padding-top: 0;
  }
  &__head{
    text-align: center;
    max-width: 840px;
    margin: 0 auto 70px;
    &-text{
      font-size: rem(16);
      line-height: rem(28);
    }
  }

  &__list{
    display: flex;
    flex-direction: column;
  }
  &__item{
    width: 50%;
    padding: 0 91px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 10px;
    height: 105px;
    @include media($md, max){
      padding: 0 15px 0 0;
    }
    &:nth-child(2n-1){
      align-self: flex-end;
      align-items: flex-start;
      @include media($md, max){
        padding: 0 0px 0 15px;
      }
      .map__line{
        left: -9px;
        right: auto;
        background: $c_alt2;
        @include media($md, max){
          left: -4px;
        }
        &:before{
          background: $c_alt2;
        }
      }
      .map__text{
        text-align: left;
      }
      .map__title{
        color: $c_alt2;
        &:before{
          left: -35px;
          right: auto;
          background: $c_alt2;
          @include media($md, max){
            left: -11px;
          }
        }
        &:after{
          left: 100%;
          right: auto;
        }
      }
    }
    &--already{
      .map__title{
        opacity: 1;
      }
      .map__line{
        opacity: 1;
      }
    }
  }
  &__title{
    font-family: $fnt_title;
    margin-bottom: 15px;
    padding: 15px 0;
    position: relative;
    color: $c_alt1;
    font-size: 21px;
    opacity: 0.5;
    @include media($md, max){
      padding: 5px 0;
      margin-bottom: 5px;
    }
    &:before{
      content: '';
      display: block;
      width: 161px;
      height: 2px;
      background: $c_alt1;
      position: absolute;
      bottom: 0px;
      right: -35px;
      @include media($md, max){
        right: -11px;
      }
    }
    &:after{
      content: '';
      display: block;
      width: 161px;
      height: 2px;
      background: #fff;
      position: absolute;
      bottom: 0px;
      right: 100%;
    }
  }
  &__text{
    font-size: 13px;
    line-height: 21px;
    text-align: right;
    @include media($md, max){
      line-height: 18px;
    }
  }

  &__line{
    position: absolute;
    width: 18px;
    height: 98px;
    right: -8px;
    top: 0;
    background: $c_alt1;
    opacity: 0.5;
    @include media($md, max){
      width: 8px;
      right: -4px;
    }
    &:before{
      content: '';
      display: block;
      width: 18px;
      height: 17px;
      border-radius: 50%;
      background: #19d770;
      bottom: -9px;
      left: 52%;
      position: absolute;
      transform: translateX(-50%);
      @include media($md, max){
        width: 8px;
        height: 8px;
        bottom: -3px;
      }
    }
    &:after{
      content: '';
      display: block;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: #fff;
      top: -8px;
      left: 51%;
      position: absolute;
      transform: translateX(-50%);
      @include media($md, max){
        width: 8px;
        height: 8px;
        top: -3px;
      }
    }
  }
}