.title{
	font-weight: bold;
	font-size: rem(52.5);
	color: $c_alt1;
	margin-bottom: 20px;
	@include media($sm, max) {
		font-size: 30px;
	}
	&--main{
	    font-size: rem(98);
		margin-bottom: 5px;
	}
	&--h3{
		font-size: rem(33.6);
		margin-bottom: 20px;
	}
	&--h4{
		font-size: rem(21);
		margin-bottom: 15px;
	}
	&--light{
		color: #fff;
	}
	&--ctype2{
		color: $c_alt2;
	}

}