.downList{
	transition: all 0.3s;
	position: relative;
	z-index: 1;
	border-radius: 20px;
	padding: 2px 10px;
	border: 2px solid #fff;
	@include media($sm, max) {
		border: 2px solid $c_alt2;
	}
	&.has-active{
		border-radius: 8px 8px 0 0;
		@include media($sm, max) {
			border: 2px solid #fff;
		}

		.downList__head{
			&:before{
				transform: rotate(-45deg) translateY(-50%);
			}
		}
		.downList__head{
			border-bottom: 1px solid $c_alt1;

			border-bottom: none;
		}
	}

	&__head{
		padding-right: 20px;
		cursor: pointer;
		position: relative;
		display: -webkit-box;
		display: flex;
		-webkit-box-align: center;
		align-items: center;
		height: 30px;
		width: 77px;
		padding-left: 10px;
		&:before{
			width: 7px;
			height: 7px;
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			right: 10px;
			transform:translateY(-50%) rotate(135deg);
			border-top: 1px solid $c_alt1;
			border-right: 1px solid $c_alt1;
			transition: all 0.3s;

			border-top: 1px solid #fff;
			border-right: 1px solid #fff;
			@include media($sm, max) {
				border-top: 1px solid $c_alt2;
				border-right: 1px solid $c_alt2
			}
		}
		&:hover{
			.downList__name{
				color: $c_alt2;
			}
			&:before{
				border-top: 1px solid $c_alt2;
				border-right: 1px solid $c_alt2;
			}
		}
	}
	&__name{
		display: inline-block;
		text-transform: uppercase;
		font-size: 13px;
		color: #fff;
		transition: all 0.3s;
		@include media($sm, max) {
			color: $c_alt2;
		}
		&--type1{
			border-bottom: transparent;
		}
	}
	&__content{
		position: absolute;
		top: 102%;
		left: -2px;
		right: -2px;
		background: #fff;
		border-radius: 0 0 8px 8px;
		padding: 0 5px;
	}
	&__list{
		padding: 5px 5px;
	}
	&__item{
		padding: 5px 0;
		padding-left: 15px;
	}
	&__link{
		font-size: 13px;
		text-decoration: none;
		display: block;
		text-transform: uppercase;
		white-space: nowrap;
		&:hover{
			text-decoration: underline;
		}
	}
}