.made{
  padding-top: 133px;
  @include media($md, max) {
    padding-top: 20px;
  }
  &__bubbles{
    display: flex;
    margin: 49px 0;
    @include media($def, max) {
      margin: 30px 0 0;
    }
    @include media($md, max) {
      justify-content: center;
    }
    &-item{
      margin-right: 40px;
      @include media($def, max){
        margin: 0;
        display: flex;
        align-items: center;
      }
      &--sm{
        @include media($sm, max) {
          //display: none;
        }
      }
    }
  }

  &__head{
    @include media($md, max){
      text-align: center;
    }
    .wrong__img{
      @include media($def, max){
        max-width: 400px;
        margin-bottom: 20px;
      }
    }
  }
  &__main{
    @include media($md, max){
      text-align: center;
    }
  }

  &__tv{
    margin: 168px 0 56px;
    min-height: 406px;
    position: relative;
    @include media($md, max) {
      margin: 80px 0 0px;
    }
    @include media($sm, max) {
      top: 80px;
    }

    &:before{
      content: '';
      display: block;
      width: 1363px;
      height: 867px;
      position: absolute;
      left: 0;
      top: 50%;
      transform:translateY(-50%);
      background: url('pic/made_bg1.png') 0 0 no-repeat;
      @include media($sm, max) {
        left: -360px;
        top: 30%;
      }
    }
  }

  &__desktop{
    position: relative;
    top: -105px;
    left: 0%;
    max-width: 936px;
    width: 128%;
    @media (max-width: 1660px){
      width: 118%;
    }
    @media (max-width: 1470px){
      width: 105%;
      top: -35px;
    }
    @media (max-width: 1280px){
      width: 105%;
      top: -15px;
    }

    &-bg{
      position: relative;
      padding-top: 59.66%;
      background: url('pic/tv.png') center center no-repeat;
      background-size: contain;
    }
    &-cnt{
      position: absolute;
      top: 5.7%;
      left: 11.8%;
      width: 76.4%;
    }
  }
}