[data-tabContent]{
  display: none;
}
.has-active[data-tabContent]{
  display: block;
}
.has-active[data-tab]{
  cursor: default;
}
.js-downListContent{
  display: none;
}
.mfp-close-btn-in .mfp-close{
  font-size: 40px;
}


.js-showBlock{
  position: relative;
  visibility: hidden;
  opacity: 0;
  top: 40px;
}

.has-animaDash{
  animation: dash 5s linear infinite;
}
.circlePath.has-animaDash{
  animation: dashBack 5s linear infinite;
}

@keyframes dash {
  to {
    stroke-dashoffset: 150;
  }
}

@keyframes dashBack {
  to {
    stroke-dashoffset: -150;
  }
}