.work{
  padding-top: 0px;
  @include media($md, max){
    text-align: center;
  }
  &__group{
    display: flex;

    justify-content: space-between;
    margin-bottom: 98px;
    @include media($def, max){
      flex-wrap: wrap;
    }
    @include media($md, max){
      margin-bottom: 35px;
    }
    &-item{
      @include media($def, max){
        width: 25%;
      }
      @include media($md, max){
        width: 50%;
        display: flex;
        justify-content: center;
        margin: -20px 0;
      }
    }
  }

  &__head{
    margin-bottom: 59px;
    &-text{
      font-size: 16px;
    }
  }
  &__main{
    position: relative;
  }

  &__line{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 80%;
    @include media($md, max){
      display: none;
    }
    svg{
      width: 100%;
    }
  }
  &__list{
    padding-bottom: 70px;
  }
  &__item{
    margin-bottom: 98px;
    @include media($md, max){
      margin-bottom: 40px;
    }
  }

  &__img{
    max-width: 560px;
    position: relative;
    @include media($md, max){
      max-width: 270px;
      margin: 0 auto 20px;
    }
    &-wrap{
      position: relative;
      z-index: 1;
    }
  }
  &__bg{
    padding-top: 78.58%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    &--type1{
      background-image: url('pic/work_bg1.png');
    }
    &--type2{
      background-image: url('pic/work_bg2.png');
    }
    &--type3{
      background-image: url('pic/work_bg3.png');
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 100%;
    transform:translate(-50%,-50%);
  }


  &__cnt{
    padding: 0 7%;
    @include media($md, max){
      padding: 0;
    }
  }


}