.progress{
  padding: 140px 0 35px;
  @include media($def, max){
    padding-top: 40px;
  }
  @include media($md, max){
    padding-top: 100px;
  }
  &__wrap{
    position: relative;
  }
  &__line{
    height: 28px;
    border-radius: 50px;
    background: #f6f7f9;
    position: relative;
    overflow: hidden;
    span{
      display: block;
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
      background: $c_alt1;
    }
  }


  &__item{
    position: absolute;
    top: 15px;
    width: 33.33%;
    display: flex;
    padding-top: 52px;
    flex-direction: column;
    align-items: flex-end;
    padding-right: 15px;
    border-right: 2px solid $c_alt1;
    @include media($md, max){
      padding-top: 30px;
      padding-right: 5px;
    }
    &:nth-child(1){
     left: 0;
    }
    &:nth-child(2){
      left: 33.33%;
    }
    &:nth-child(3){
      left: 65.3%;
    }
  }
  &__index{
    position: absolute;
    bottom: 100%;
    left: 100%;
    transform:translate(-50%,50%);
    border-radius: 50%;
    width: 18px;
    height: 18px;
    background: $c_alt1;
    &.has-active{
      background: #fff;
    }
  }

  &__title{
    font-size: rem(21);
    font-weight: bold;
    margin-bottom: 5px;
    @include media($md, max){
      font-size: 13px;
    }
  }
  &__price{
    color: $c_alt1;
    font-size: rem(21);
    font-weight: bold;
    @include media($md, max){
      font-size: 13px;
    }
  }
}