.scBlock{
  height: 2500px;
  padding: 0;
  position: relative;
  .view__size{
    @include media( 1920px ) {
      transform: none;
    }
  }
  &__layaut{
    padding-bottom: 0;
    //min-height: 769px;
    //display: flex;
    //align-items: flex-start;
    background: #e8faef url('pic/city_bg.png') center top no-repeat;
    border-bottom: 2px solid $c_alt2;
    @include media($lg, max){
      background-size: contain;
    }
    &.has-toTop{
      position: absolute;
      bottom: 20px;
      width: 100%;
      left: 0;
    }
    &.has-fixed{
      position: fixed;
      bottom: 20px;
      width: 100%;
      left: 0;
    }
  }
  &__main{
    width: 100%;
    height: 100%;
    .view__size{
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: space-between;
      &:after{
        display: none;
      }
    }

  }
  &__slider{
    //min-height: 430px;
    padding-bottom: 20px;
    padding-top: 21px;
    position: relative;
    top: 20px;
    @include media($md, max){
      top: 10px;
    }
  }
  &__pagin{
    display: flex;
    justify-content: center;
    //margin-bottom: 21px;
    @include media($md, max){
      margin-bottom: 0;
    }
    &-item{
      width: 10px;
      margin: 0 7px 5px;
      height: 10px;
      border-radius: 50%;
      border: 2px solid $c_alt2;
      &.has-active{
        background: $c_alt2;
      }
    }
  }
  &__list{
    max-width: 700px;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    position: relative;
  }
  &__slide{
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transition: all 0.3s;
    &.has-active{
      opacity: 1;
      visibility: visible;
    }
  }
}