.mobile{
	//position: fixed;
	//z-index: 10;
	left: 0;
	top: 0;
	width: 100%;
	position: relative;	
	display: block;
	display: none;
	@include media($def, min) {
		display: none;
	}
	&.has-menu{
		.mobile__wrap{
			transform: translate(0%);
		}
	}
	&__head{
		min-height: 45px;
		display: flex;
		justify-content: space-between;
		background: #333;
		align-items: center;
	}
	&__logo{
		color: #fff;
	}
	&__center{
		color: #fff;
	}
	&__button{
		display: none;
	    position: relative;
	    top: 0px;
	    right: 0px;
	    overflow: hidden;
	    z-index: 15;
	    padding: 0;
	    width: 45px;
	    height: 45px;
	    font-size: 0;
	    border-radius: 10px;
	    border: none;
	    background: none;
	    cursor: pointer;
	    transition: all 0.3s;
	    &:focus{
	    	outline: none;
	    }
        @include media($def, max) {
	        display: block;
		}

	    & span{
	    	display: block;
		    position: absolute;
		    left: 10px;
		    right: 10px;
		    height: 4px;
		    background: #fff;
		    top: 22px;
		    transition: all 0.3s;
			@include media($sm, max) {
				background: $c_alt2;
			}
		    &:before{
		    	position: absolute;
				display: block;
				left: 0;
				width: 100%;
				height: 4px;
				background-color: #fff;
				content: "";
				top: -7px;
				transition: all 0.3s;
				@include media($sm, max) {
					background: $c_alt2;
				}
		    }
		    &:after{
		    	position: absolute;
				display: block;
				left: 0;
				width: 100%;
				height: 4px;
				background-color: #fff;
				content: "";
				bottom: -7px;
				transition: all 0.3s;
				@include media($sm, max) {
					background: $c_alt2;
				}
		    }
	    }
	    &.has-active{
	    	& span{
	    		background: none !important;
	    		&:before{
	    			top: 0;
					transform: rotate(45deg);
	    		}
	    		&:after{
	    			bottom: 0;
					transform: rotate(-45deg);
	    		}
	    	}
	    }
	}
	&__wrap{
		width: 320px;
		height: 100%;
		//overflow-y: scroll;
		background: #000;
		position: fixed;
		z-index: 15;
		box-sizing: border-box;
		top: 0;
		left: 0;
		transform: translate(-100%);
		transition: all 0.5s;
		overflow: hidden;

		flex-direction: column;
		justify-content: space-between;
	}
	&__scroll{
		height: 100%;
		overflow-y: scroll;
	}
	&__top{
		height: 50px;
		background: #ccc;
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		width: 100%;
	}
	&__bottom{
		height: 50px;
		background: #ccc;
		bottom: 0;
		left: 0;
		width: 100%;
		position: absolute;
		z-index: 2;
	}
	&__content{
		flex-grow:1;
		position: relative;
		overflow-y: scroll;
		z-index: 1;
		padding: 10px 0;
		height: 100%;
		box-sizing: border-box;
	}


}