.team{
  padding: 90px 0 150px;
  min-height: 593px;
  @include media($def, max){
    padding-bottom: 70px;
  }
  &__head{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 56px;
  }
  &__title{
    position: relative;
    &:before{
      content: '';
      display: block;
      position: absolute;
      top: -90px;
      left: 50%;
      width: 827px;
      height: 593px;
      transform: translateX(-50%);
      background: url('pic/team_bg.png') center top no-repeat;
    }
  }

  &__list{
    margin-left: -17px;
    margin-right: -17px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    padding: 0 17px;
    margin-bottom: 35px;
    position: relative;
    width: 33.33%;
    @include media($def, max){
      width: 100%;
      margin-bottom: 20px;
      top: 0 !important;
    }
    &:nth-child(3n-1){
      top: 35px;
    }
    &:nth-child(3n-2){
      top: 70px;
    }
  }
  &__wrap{
    background: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    display: flex;
    width: 100%;
    align-items: stretch;
    min-height: 294px;
    &.has-hover{
      &:hover{
        .team__img{
          &:before{
            background: -moz-linear-gradient(top, rgba(25,215,112,0) 0%, rgba(37,161,182,0) 50%, rgba(49,106,251,1) 100%);
            background: -webkit-linear-gradient(top, rgba(25,215,112,0) 0%,rgba(37,161,182,0) 50%,rgba(49,106,251,1) 100%);
            background: linear-gradient(to bottom, rgba(25,215,112,0) 0%,rgba(37,161,182,0) 50%,rgba(49,106,251,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0019d770', endColorstr='#316afb',GradientType=0 );
          }
        }
        .title{
          color: $c_alt2;
        }

        .team__more{
          color: $c_alt2;
        }
        .team__social-text{
          color: $c_alt2;
        }
        .team__social-item{
          svg{
            fill: $c_alt2;
          }
        }
      }
    }

    &--popup{
      min-height: 630px;
      border-radius: 10px;
      overflow: hidden;
      background: transparent;
      .team__img{
        width: 47%;
      }
      .team__cnt{
        width: 53%;
        background: #fff;
        @include media($sm, max){
          padding-left: 5px;
          padding-right: 5px;
        }


      } 
      .team__img{
        &:before{
          display: none;
        }
      }
      .team__social-icon{
        @include media($sm, max){
          display: none;
        }
      }
    }
  }
  &__img{
    width: 40%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    transition: all 0.3s;
    @include media($def, max){
      width: 20%;
    }
    &:before{
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      transition: all 0.3s;
      background: -moz-linear-gradient(top, rgba(25,215,112,0) 0%, rgba(25,215,112,0) 50%, rgba(25,215,112,1) 100%);
      background: -webkit-linear-gradient(top, rgba(25,215,112,0) 0%,rgba(25,215,112,0) 50%,rgba(25,215,112,1) 100%);
      background: linear-gradient(to bottom, rgba(25,215,112,0) 0%,rgba(25,215,112,0) 50%,rgba(25,215,112,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0019d770', endColorstr='#19d770',GradientType=0 );
    }
  }
  &__cnt{
    width: 60%;
    padding: 22px 21px 7px;
    @include media($def, max){
      width: 80%;

    }
  }
  &__text{
    font-size: 13px;
    line-height: 21px;
    margin-bottom: 18px;
  }
  .team__more{
    color: $c_alt1;
    cursor: pointer;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }

  &__social{
    &-item{
      display: block;
      margin-bottom: 5px;
      display: flex;
      align-items: center;
    }
    &-icon{
      width: 28px;
      display: inline-block;
      margin-right: 10px;
      svg{
        fill: $c_alt1;
      }

    }
    &-text{
      font-size: 13px;
      color: $c_alt1;
    }
  }
}