.faq{
  padding: 100px 0 100px;
  min-height: 460px;
  @include media($def, max){
    min-height: 300px;
  }
  &__item{
    padding: 15px 0;
    border-bottom:2px solid #d9e7ff;
    &.has-open{
      border-bottom:2px solid $c_alt1;
    }
  }
  &__title{
    position: relative;
    &:before{
      content: '';
      display: block;
      position: absolute;
      top: -70px;
      left: -50%;
      //transform:translate(-50%,-50%);
      z-index: -1;
      width: 598px;
      height: 429px;
      background: url('pic/f.png') 0 0 no-repeat;
      @include media($def, max){
        top: -160px;
        left: -360%;
      }
      @include media($md, max){
        top: -140px;
        left: -60%;
        opacity: 0.4;
     }
    }
  }
  &__head{
    position: relative;
    padding-right: 35px;
    span{
      font-size: 21px;
      color: $c_alt2;
      cursor: pointer;
      font-family: $fnt_title;
      @include media($md, max){
        font-size: 18px;
      }
    }
    &:before{
      content: '';
      display: block;
      right: 0;
      top: 50%;
      width: 28px;
      height: 28px;
      position: absolute;
      background: url('pic/plus.png') center center no-repeat;
      background-size: 28px 28px;
      transform:translateY(-50%);
      cursor: pointer;
    }
    &.has-open{
      span{
        color: $c_alt1;
      }
      &:before{
        background: url('pic/minus.png') center center no-repeat;
        background-size: 28px 28px;
      }

    }
  }

  &__content{
    padding: 17px 0;
    display: none;
    span{
      font-size: 16px;
      line-height: 33px;
    }
  }
}