.partners{
  padding: 49px 0 83px;
  @include media($def, max){
    padding-bottom: 20px;
  }
  @include media($md, max){
    text-align: center;
  }
  &__group{
    padding: 24px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    &-item{
      width: 50%;
      display: flex;
      align-items: center;
      margin-bottom: 34px;
      @include media($md, max){
        width: 100%;
        flex-wrap: wrap;
      }
      &--full{
        width: 84%;
        @include media($lg, max){
          width: 100%;
        }
        .partners__group-img{
          width: 19%;
          @include media($lg, max){
            width: initial;
          }
          @include media($md, max){
            width: 100%;
            display: flex;
            justify-content: center;
          }
        }
        .partners__group-cnt{
          width: 81%;
          @include media($md, max){
            width: 100%;
          }
        }
        .partners__group-text{
          max-width: 434px;
          @include media($md, max){
            max-width: 100%;
          }
        }
      }
    }
    &-img{
      //width: 33%;
      //max-width: 147px;
      @include media($md, max){
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }
    &-cnt{
      width: 67%;
      padding: 0 15px;
      @include media($md, max){
        width: 100%;
        padding: 0;
      }

    }
    &-title{
      font-size: 15px;
      margin-bottom: 10px;
      font-family: $fnt_title;
    }
    &-text{
      font-size: 12px;
      line-height: 16px;
      max-width: 254px;
      @include media($md, max){
        max-width: 100%;
      }
    }
  }

  &__cnt{
    display: flex;
    padding: 15px 16%;
    width: 100%;
    min-height: 518px;
    align-items: center;
    justify-content: center;
    @include media($lg, max){
      padding: 15px 5%;
      width: 100%;
    }
    @include media($md, max){
      padding: 15px 5px;
    }
    position: relative;
    &:before{
      content: '';
      display: block;
      z-index: -1;
      background: url('pic/partners_bg.jpg') center center no-repeat;
      width: 600px;
      height: 450px;
      background-size: 600px 450px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
  &__wrap{
    max-width: 546px;
    .view-text > *{
      color: #fff;
    }
    p{
      font-size: 14px;
    }
  }

  &__item{
    position: relative;
    border-bottom: 2px solid #b4cefc;
    &--slow{
      padding: 17px 0;
      padding-left: 20%;
      margin-bottom: 150px;
    }
    &--speed{
      padding: 15px 0;
      padding-right: 20%;
    }
  }
  &__title{
    font-size: rem(14);
    font-weight: bold;
    color: $c_alt2;
    &--big{
      font-size: rem(23);
    }
  }

  &__slow{
    position: absolute;
    left: 0;
    bottom: -3px;
  }
  &__speed{
    position: absolute;
    right: 10%;
    bottom: -3px;
  }
}