.story{
  &__wrap{
    max-width: 429px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    transform: translateX(-12%);
    @include media($lg, max){
      width: 35%;
    }
  }
  &__img{
    position: relative;
    z-index: 1;
  }
  &__bg{
    padding-top: 70.3%;
    background: url('pic/story_main.png') center top no-repeat;
    background-size: contain;
    position: relative;
  }
  &__face{
    width: 14%;
    position: absolute;
    left: 54.3%;
    top: 7.8%;
    &-item{
      //background: #000;
      padding-top: 128.73%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.3s;
      &--type1{
        background: url('pic/face_2.svg') 0 0 no-repeat;
        background-size: contain;
      }
      &--type2{
        opacity: 0;
        background: url('pic/face_1.svg') 0 0 no-repeat;
        background-size: contain;
      }
    }
    &.has-active{
      .story__face-item--type1{
        opacity: 0;
      }
      .story__face-item--type2{
        opacity: 1;
      }
    }
  }
  &__item{
    width: 100%;
  }

  &__icon{
    position: absolute;
    z-index: 1;
    max-width: 36%;
    width: 36%;
    transform: translate(-50%,-50%);
    &--1{
      //top: 57%;
      //left: 61%;
      top: 18%;
      left: -52%;
    }
    &--2{
      //top: 57%;
      //left: 61%;
      top: 18%;
      left: 174%;
    }
  }
  &__dashed{
    position: absolute;
    z-index: 0;
    max-width: 100%;
    width: 100%;
    transform: translate(-50%,-50%);
    svg{
      width: 100%;
    }
    &--1{
      top: 29%;
      left: 13%;
    }
    &--2{
      top: 29%;
      left: 122%;
    }
  }

  &__bubble{
    position: absolute;
    top: -40%;
    left: 65%;
    transform:  scale(0);
    .bubbles__title{
      font-size: rem(30);
    }
  }


}